import React from 'react';
import { List, Avatar, Button } from 'antd';
import { LikeOutlined, StarOutlined } from '@ant-design/icons';

// 自定义列表 及列表行组件

const CustomList = ({ listData, randerItem, header, setSelectedItem, selectButtonOnClick, setVisible, visible }) => {
    // 自定义列表
    return (
        <List
            itemLayout="vertical"
            size="large"
            header={<div>{header}</div>}
            footer={null}
            bordered
            dataSource={listData}
            renderItem={(item) => randerItem(item, setSelectedItem, selectButtonOnClick, setVisible, visible)}
        />
    )
}
CustomList.defaultProps = {
    listData: [],
    randerItem: (item) => <div>{item}</div>,
    header: '列表标题',
    setSelectedItem: (item) => console.log(item),
    selectButtonOnClick: (item) => console.log(item),
    setVisible: (num) => console.log(num),
    visible: 0,
}

const customRanderItem = (
    item = {
        id: 1,
        actions: [
            <Button icon={<StarOutlined />} onClick={() => console.log('关注1024')}>1024</Button>,
            <Button icon={<LikeOutlined />} onClick={() => console.log('点赞1024')}>1024</Button>
        ],
        img: '',
        avatar: '',
        title: 'List.Item model',
        description: '说明文字',
        content: '内容文字',
    },
    imgWidth = 260
) => {
    // 自定义渲染list 的 item 函数 item = {id: 1, }
    return (
        <List.Item
            key={item.id}
            actions={item.actions}
            extra={item.img.length > 0 ? <img width={imgWidth} alt="image" src={item.img} /> : null}
        >
            <List.Item.Meta
                avatar={item.avatar.length > 0 ? <Avatar src={item.avatar} /> : null}
                title={item.title}
                description={item.description}
            />
            {item.content}
        </List.Item>
    )
}

export { CustomList, customRanderItem };
