import React, { useState } from 'react';
import { Drawer, Button, Select, DatePicker, Space } from 'antd';

const { Option } = Select;

// 列表查询 form

const PurchaseRecordsQueryDrawerForm = ({ visible, setVisible, queryHandle }) => {

    const [dayOrMonth, setDayOrMonth] = useState('d');
    const [selectedDate, setSelectedDate] = useState('2020-10');

    const queryDayOrMonth = (val) => {
        // 选择按日或月统计
        setDayOrMonth(val);
    }

    const dateOnChange = (dateVal) => {
        const dateFormat = ['YYYY-MM-DD', 'YYYY-MM']
        let selectNum = 1;
        if (dayOrMonth === 'd') {
            selectNum = 0
        }
        const fdate = dateVal.format(dateFormat[selectNum]);
        console.log(fdate);
        setSelectedDate(fdate);
    }

    const onFinish = () => {
        // 提交查询 根据具体情况更新 fromObj
        console.log(selectedDate);
        queryHandle({'add_time': selectedDate});
        setVisible(0);
    };

    return (
        <>
            <Drawer
                title="销售记录统计"
                width="80%"
                height="300px"
                placement="top"
                onClose={() => setVisible(0)}
                visible={visible === 2}
                bodyStyle={{ paddingBottom: 20 }}
                footer={<Button onClick={onFinish}>提交</Button>}
            >
                <Space direction="vertical">
                    <Select defaultValue="day" style={{ width: 120 }} onChange={queryDayOrMonth}>
                        <Option value="d">按日统计</Option>
                        <Option value="m">按月统计</Option>
                    </Select>

                    {dayOrMonth === 'd'
                        ? <DatePicker size="large" onChange={dateOnChange} />
                        : <DatePicker size="large" onChange={dateOnChange} picker="month" />}

                </Space>
            </Drawer>
        </>
    );
};

PurchaseRecordsQueryDrawerForm.defaultProps = {
    visible: false,
    setVisible: (booleanValue) => { console.log(booleanValue); },
    queryHandle: (queryParamsObj) => { console.log(queryParamsObj); }
};

export default PurchaseRecordsQueryDrawerForm;
